      <p class="p2">
        The Tinian landscape cannot be apart from the pigeon houses that the inhabitants built due to socio-economic
        reasons. The pigeon houses were buildings-tools for the farmers of the previous generation. Pigeons were
        valuable for many reasons, such as for their meat, their feces, as well as they were messengers. So, the need
        for the animals to be protected, along with the need for rural storage space led to their erection.
      </p>
      <img class="img3" src="../../../images/Architecture/lectures/2pigeon-houses/3.jpg" alt="" />
      <img class="img4" src="../../../images/Architecture/lectures/2pigeon-houses/4.jpg" alt="" />
      <p class="p3">
        In order for the modern people to protect them as a precious part of their tradition, they should learn the
        reason of their presence, the methods and conditions of their construction (which are analyzed extensively in
        this lecture). But most importantly, they should understand the reason why they are valuable.
      </p>
      <img class="img5" src="../../../images/Architecture/lectures/2pigeon-houses/5.jpg" alt="" />
      <img class="img6" src="../../../images/Architecture/lectures/2pigeon-houses/6.jpg" alt="" />
